@import url(https://fonts.googleapis.com/css?family=Roboto:400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video,button{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}html{height:100%;background-color:#fafafa}body{height:100%;font-family:"Roboto",sans-serif}#root{display:flex;flex-direction:column;justify-content:space-between;height:100%}header{display:flex;justify-content:space-between;align-items:center;height:98px;background-color:#3f51b5;box-shadow:0px 2px 2px rgba(0,0,0,.3)}header h1{font-size:2rem;padding-left:16px;color:#fff;flex:1 1}header nav{display:flex;justify-content:space-between;align-items:center;width:1000px;padding-right:16px;flex:1 1}main{display:flex;justify-content:center;align-items:center}main .card{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100px;height:100px;font-size:2rem;box-shadow:0px 2px 2px rgba(0,0,0,.3);border-radius:4px}footer{display:flex;justify-content:space-around;align-items:center;height:96px;background-color:#3f51b5}footer .tool{display:flex;justify-content:space-between}footer .tool p{color:#fff;padding:8px}button{color:#fff;font-weight:bold;border-style:solid;border-width:1px;border-color:#e0e0e0;background-color:#3f51b5;padding:8px;border-radius:8px}
.grid{border-style:solid;border-width:2px;grid-column-gap:1px;grid-row-gap:1px;background-color:#666}.grid div{background-color:#ccc;text-align:center;transition-duration:.05s;font-size:2rem;cursor:-webkit-grabbing;cursor:grabbing}.grid .line{border-style:solid;border-bottom-color:#000;border-bottom-width:5px}.strikethrough{position:relative}.strikethrough:before{position:absolute;content:"";left:0;top:50%;right:0;border-top:2px solid;border-color:inherit;transform:rotate(-45deg)}
.calculator{display:flex;flex-direction:column;justify-content:space-between;background-color:#000}.calculator .numbers{display:grid;font-weight:bold;font-size:2.5rem;background-color:#000;grid-template-columns:repeat(3, 4rem);grid-template-rows:repeat(4, 4rem);grid-column-gap:1px;grid-row-gap:1px;cursor:-webkit-grabbing;cursor:grabbing}.calculator .numbers div{display:flex;justify-content:center;align-items:center;width:4rem;height:4rem;background-color:#8a8a8b;color:#000}.calculator .numbers #zero{grid-column:2}.calculator .symbols{display:grid;font-weight:bold;font-size:2.5rem;background-color:#000;grid-template-columns:repeat(3, 4rem);grid-template-rows:repeat(3, 4rem);grid-column-gap:1px;grid-row-gap:1px;cursor:-webkit-grabbing;cursor:grabbing}.calculator .symbols div{display:flex;justify-content:center;align-items:center;width:4rem;height:4rem;background-color:#8a8a8b;color:#000}
.modal{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,.6)}.modal section{position:fixed;transform:translate(-50%, -50%);display:flex;flex-direction:column;align-items:center;justify-content:space-around;width:30%;padding:5rem 0rem;background:#fff;height:auto;top:50%;left:50%}.modal section img{width:400px}.hidden{display:none}
.login-card{padding:40px;width:274px;background-color:#f7f7f7;margin:0 auto 10px;border-radius:2px;box-shadow:0px 2px 2px rgba(0,0,0,.3);overflow:hidden}.login-card h1{font-weight:100;padding-bottom:16px;text-align:center;font-size:2.3em}.login-card input[type=submit]{width:100%;display:block;margin-bottom:10px;position:relative}.login-card input[type=text],.login-card input[type=password]{height:44px;font-size:16px;width:100%;margin-bottom:10px;-webkit-appearance:none;background:#fff;border:1px solid #d9d9d9;border-top:1px solid silver;padding:0 8px;box-sizing:border-box;-moz-box-sizing:border-box}.login-card input[type=text]:hover,.login-card input[type=password]:hover{border:1px solid #b9b9b9;border-top:1px solid #a0a0a0;box-shadow:inset 0 1px 2px rgba(0,0,0,.1)}.login-card .login{text-align:center;font-size:14px;font-family:"Arial",sans-serif;font-weight:700;height:36px;padding:0 8px}.login-submit{border:0px;color:#fff;text-shadow:0 1px rgba(0,0,0,.1);background-color:#4d90fe}.login-submit:hover{border:0px;text-shadow:0 1px rgba(0,0,0,.3);background-color:#357ae8}.login-card a{text-decoration:none;color:#666;font-weight:400;text-align:center;display:inline-block;opacity:.6;transition:opacity ease .5s}.login-card a:hover{opacity:1}.login-help{width:100%;text-align:center;font-size:12px}.error{margin-top:8px;padding:8px;background-color:#fdecea;color:#611a15;border-radius:2px}
